.container {
  height: 100%;
  width: 100%;
  background-color: #201f1e;
  // background-color: #eee;
  color: #e3e3e3;
  position: relative;
  user-select: none;
  .panel {
    display: flex;
    flex-flow: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 32px 32px 0 32px;
  }
  .bgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .copyright {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  .topPanel {
    display: flex;
    width: 100%;
    margin-bottom: 16px;
    align-items: center;
    .nowPanel {
      flex: 1;
      margin-right: 16px;
      .weatherNowtmp {
        display: flex;
        flex-wrap: nowrap;
        .nowTmp {
          font-size: 80px;
          line-height: 76px;
          height: 76px;
          margin-right: 20px;
        }
        .info {
          text-align: left;
          & > div {
            margin: 4px 0;
            line-height: 20px;
            height: 20px;
            font-size: 13px;
            width: 200px;
            span {
              margin-left: 8px;
            }
          }
        }
      }
    }
    .weatherInfo {
      width: 100%;
      margin-left: 16px;
      display: flex;
      font-size: 12px;
      flex: 1;
      flex-wrap: wrap;
      align-items: center;
      align-content: space-around;
      height: 100%;
      & > div {
        width: 33.33%;
      }
    }
  }
  .hoursPanel {
    width: 100%;
    margin-bottom: 16px;
    &.listPanel {
      ul li {
        padding: 2px 0;
      }
    }
    .linePanel {
      top: 42px;
      bottom: 2px;
    }
  }
  .weekPanel {
    width: 100%;
    margin-bottom: 48px;
    .linePanel {
      top: 50px;
      bottom: 30px;
    }
  }

  .listPanel {
    display: flex;
    flex-direction: column;
    padding: 12px;
    border-radius: 6px;
    background-color: rgba(255, 255, 255, 0.03);
    h3 {
      font-size: 12px;
      margin-bottom: 8px;
      color: rgba(255, 255, 255, 0.4);
    }
    ul {
      list-style: none;
      display: flex;
      flex: 1;
      position: relative;
      li {
        &:first-child {
          background-color: rgba(255, 255, 255,0.08);
          border-left: none;
        }
        &:hover {
          background-color: rgba(255, 255, 255,0.06);
        }
        width: 100%;
        font-size: 12px;
        padding: 10px 0;
        border-left: 1px solid rgba(255, 255, 255, .05);
        text-align: center;
        transition: background .2s;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        z-index: 1;
        .text {
          line-height: 20px;
        }
      }
    }
    .linePanel {
      position: absolute;
      width: 100%;
      z-index: 0;
      & > div {
        width: 100%;
        height: 100%;
      }
    }
  }

  .flex1 {
    flex: 1;
  }

  .flex2 {
    flex: 2;
  }

  .flex4 {
    flex: 4;
  }

}
